import { Component, OnInit, Injector, ViewChild, TemplateRef, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { Router } from '@angular/router'
import { ModalService } from 'src/app/shared/services/modal.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as _ from 'underscore';

@Component({
  selector: 'app-expense-subledger',
  templateUrl: './expense-subledger.component.html',
  styleUrls: ['./expense-subledger.component.scss']
})
export class ExpenseSubledgerComponent implements OnInit {
  
  isSubLedgerDataLoaded:boolean = false;

  subLedgerData:string = "";
  subLedgerDataList:any;

  vendorDataList:any;
  vendorCategoryData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "vendorId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  totalDueAmount:number = 0;
  netDueAmount:number = 0;

  blockListData:any;

  filterSelected:string = "all";
  singleBlock:string = "Select Tower";
  selectedBlockId:number;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  dialogRef:any;

  selectedInput:string = "";
  columnField: any = {}
  
  public params = {
    apartmentId: this.sessionService.apartmentId
  }


  modalService:any;
  columnData:any;

  @ViewChild("viewInvoiceTypeRef", { static: false }) viewInvoiceTypeRef: TemplateRef<any>;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  
  constructor(
  	private injector: Injector,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private vendorService:VendorService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
    private sessionService:SessionService,
    private router:Router
  ) { this.modalService = this.injector.get(ModalService);}


  getDueAmount(value){
  	return value > 0 ? 'high' : 'low';
  }

  getSelected(type){
  	return this.filterSelected == type ? 'selected' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  getVendorCategory(ledger, id){

    var data = _.filter( this.vendorDataList, function( item ) {
      if(item.vendorId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      
      var categoryId = data[0].vendorCategoryId;

      var name = _.filter( this.vendorCategoryData, function( item ) {
        if(item.lookupValueId === categoryId)
          return item;
      });

      if(name === undefined || name.length == 0){
        return '';
      }
      else {

        this.subLedgerDataList.map(obj=> {
          if(obj.vendorId == ledger.vendorId){
            obj.vendorCategory = name[0].lookupValueName.toLowerCase();
          }
        });

        return name[0].lookupValueName;
      }

    }

  }


  dialogClose(){
    this.dialogRef.close();
  }
 
  onGlSearchFilter() {

    if(this.subLedgerData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.subLedgerData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onActions', ['$event.detail'])
  onActions(detail) {

    let ledger = this.datagrid.getrowdata(detail.rowId)

    this.sharedService.setActionIncomeTrackerIndex(ledger.vendorId)
    this.router.navigateByUrl('/ams/expense/actions/account-history/view/' + ledger.vendorId);

  }

  @HostListener('window:onInvoiceDropDown', ['$event.detail'])
  onInvoiceDropDown(detail) {

    let ledger = this.datagrid.getrowdata(detail.rowId)

    this.dialog.open(this.viewInvoiceTypeRef, {
      panelClass: 'material-dialog-small',
      data: ledger
    });

  }
 

  @HostListener('window:onviewHistory', ['$event.detail'])
  onviewHistory(detail) {
    let ledger = this.datagrid.getrowdata(detail.rowId)  
    this.router.navigate(['/ams/expense/view-invoice-history/'+ ledger.vendorId +''])
  }

  @HostListener('window:onviewPayment', ['$event.detail'])
  onviewPayment(detail) {
    let ledger = this.datagrid.getrowdata(detail.rowId)  
    this.router.navigate(['/ams/expense/post-payment/'+ ledger.vendorId +''])
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Vendor ID',
         datafield: 'vendorId',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Vendor Name',
         datafield: 'vendorName',
         cellsrenderer: cellsrenderer,
         minwidth: 200,
         renderer: columnrenderer
     }, {
         text: 'Vendor Category',
         datafield: 'vendorCategoryName',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Due',
         datafield: 'due',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Status',
         datafield: 'status',
         minwidth: 170,
         cellsrenderer: (row, column, value) => {
          return '<div style="padding: 14px; overflow: hidden; text-overflow: ellipsis; text-align: center"><span class="dots rt-3 '+ getClassName(value) +'"></span></div>'
         },
         renderer: columnrenderer
     }, {
         text: 'Actions',
         cellsalign: 'right', 
         align: 'right',
         cellclassname: 'action-cell',
         minwidth: 170,
         cellsrenderer: (row: number): string => {

          return '<div class="simple-actions simple-action-index'+ row +'">'
                    + '<a href="javascript:void(0)" class="mat-flat-button button-md n-text bg-pink-800 text-gray-200 mr-2" onClick="invoiceDropDownEvent('+ row +')" id="invoiceDropDown">'
                      + '<div class="text-smr"><span class="text-sm mr-2">+</span>Invoice</div>'
                    + '</a>'
                    + '<a href="javascript:void(0)" class="mat-flat-button button-md n-text bg-gray-500 text-gray-100" onClick="actionEvent('+ row +')">'
                      + '<div class="text-smr">Action</div>'
                    + '</a>'
                 + '</div>'
        },         
         renderer: columnrenderer
     }];

    this.accountsService.getExpenseTrackerInvoicesByApartmentId(this.params).subscribe((res:any) => {

      this.subLedgerDataList = res;
      this.totalItems = this.subLedgerDataList.length;

      this.subLedgerDataList=new jqx.dataAdapter({
        localdata: this.subLedgerDataList,
        datatype: "array"
      });
      
      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.netDueAmount = 0;
      this.totalDueAmount = 0;

    _.each(this.subLedgerDataList._source.localdata, (item, index) => {

      this.netDueAmount = this.netDueAmount + item.due;

      if(item.due >= 0)
      this.totalDueAmount = this.totalDueAmount + item.due;

    });

    let lookUp = {
      LookupTypeId: 57,
      ApartmentId: this.sessionService.apartmentId
    }
    this.lookupService.getLookupValueByLookupTypeId(lookUp).subscribe((res:any) => {
        
     this.vendorCategoryData = res.filter( item => {
        return item.isActive;
     });

     this.vendorService.getVendorByApartmentId(this.params).subscribe((res:any) => {

      this.vendorDataList = res;
      this.isSubLedgerDataLoaded = true;

    });

    },
    error => {

    });

    });

  }

}

let getClassName = value => {
  return value != 'Due' ? 'bg-red-900' : 'bg-green-900';
}
(window as any).getClassName = getClassName;

let showSingleInvoiceEvent = row => {
  var event = new CustomEvent('onShowSingleInvoice', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showSingleInvoiceEvent = showSingleInvoiceEvent;

let showMultiInvoiceEvent = row => {
  var event = new CustomEvent('onShowMultiInvoice', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showMultiInvoiceEvent = showMultiInvoiceEvent;

function viewHistory(value){
  var event = new CustomEvent('onviewHistory', {
    detail: {
        rowId: value
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewHistory = viewHistory;

function viewPayment(row){
  var event = new CustomEvent('onviewPayment', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewPayment = viewPayment;

let actionEvent = row => {
  var event = new CustomEvent('onActions', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).actionEvent = actionEvent;

let invoiceDropDownEvent = row => {
  var event = new CustomEvent('onInvoiceDropDown', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).invoiceDropDownEvent = invoiceDropDownEvent;